<template>
  <div>
    <h2></h2>
    <el-tabs v-model="activeName">
      <el-tab-pane label="" name="templete">
        <el-row>
          <el-col :span="8" class="upload">
            <el-button class="downloadExcel"><a v-bind:href="`${attr}/stu/exportModel`
"></a></el-button>
            <el-upload
              class="upload-demo"
              v-bind:action="`${attr}/stu/add`"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              :before-remove="beforeRemove"
              multiple
              :on-success="addSuccess"
              :on-error="addError"
              :limit="3"
              :on-exceed="handleExceed"
              :file-list="fileList">
              <el-button size="small" type="primary"></el-button>
              <div slot="tip" class="el-upload__tip"></div>
            </el-upload>
          </el-col>
        </el-row>
      </el-tab-pane>
      <el-tab-pane label="" name="single">
        <el-row>
          <el-col :span="10" :offset="6">
            <el-form ref="tempStudentInfo" :model="tempStudentInfo" label-width="140px">
              <el-form-item label="">
                <el-input v-model="tempStudentInfo.no"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="tempStudentInfo.name"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-radio-group v-model="tempStudentInfo.gender" size="medium">
                  <el-radio-button label="" ></el-radio-button>
                  <el-radio-button label=""></el-radio-button>
                </el-radio-group>
              </el-form-item>
              <el-form-item label="">
                <el-select v-model="tempStudentInfo.schoolNo" placeholder="">
                  <el-option
                    v-for="item in parties"
                    :key="item.no"
                    :label="item.name"
                    :value="item.no">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="tempStudentInfo.nation"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-date-picker
                  v-model="tempStudentInfo.birth"
                  align="right"
                  type="date"
                  placeholder="">
                </el-date-picker>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="tempStudentInfo.type"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="tempStudentInfo.grade"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="tempStudentInfo.position"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-date-picker
                  v-model="tempStudentInfo.applyDate"
                  align="right"
                  type="date"
                  placeholder="">
                </el-date-picker>
              </el-form-item>
              <el-form-item label="">
                <el-date-picker
                  v-model="tempStudentInfo.beDevelopDate"
                  align="right"
                  type="date"
                  placeholder="">
                </el-date-picker>
              </el-form-item>
              <el-form-item label="">
                <el-date-picker
                  v-model="tempStudentInfo.beActivistDate"
                  align="right"
                  type="date"
                  placeholder="">
                </el-date-picker>
              </el-form-item>
              <el-form-item label="">
                <el-select v-model="tempStudentInfo.power">
                  <el-option v-for="item in powerOption" :key="item.value" :label="item.label" :value="item.value">

                  </el-option>
                </el-select>
              </el-form-item>
              <el-row>
                <el-col :span="12" :offset="10">
                  <el-button @click="insertSingleStudent()"></el-button>
                </el-col>
              </el-row>
            </el-form>
          </el-col>
        </el-row>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
  let util = require("../../util/utils");
  let config = require("../../config/config");
    const ip = config.ip;
    let $ = require("jquery");
    let moment = require("moment");
    let _ = require("underscore");
    export default {
        name: "insert-student",
        data(){
          return{
            attr:`http://${ip}`,
            parties:[],
            fileList:[],
            activeName:"templete",
            tempStudentInfo:{},
            powerOption:[
              {
                value:2,
                label:""
              },
              {
                value:3,
                label:""
              },
              {
                value:4,
                label:""
              },
              {
                value:5,
                label:""
              }
            ]
          }
        },
      methods: {
        addError(){
          this.$message.error("");
        },
        addSuccess(response){
          // util.redict(response);
          if(response.code == 1001){
            this.$message(`,${response.data}`);
          }else{
            this.$message.error(`,${response.msg},${response.data}`)
          }
        },
        handleRemove(file, fileList) {
          console.log(file, fileList);
        },
        handlePreview(file) {
          console.log(file);
        },
        handleExceed(files, fileList) {
          this.$message.warning(` 3  ${files.length}  ${files.length + fileList.length} `);
        },
        beforeRemove(file, fileList) {
          return this.$confirm(` ${ file.name }`);
        },
        insertSingleStudent(){
          let self = this;
          this.tempStudentInfo.birth = moment(this.tempStudentInfo.birth).format("YYYY/MM/DD");
          this.tempStudentInfo.applyDate = moment(this.tempStudentInfo.applyDate).format("YYYY/MM/DD");
          this.tempStudentInfo.beDevelopDate = moment(this.tempStudentInfo.beDevelopDate).format("YYYY/MM/DD");
          this.tempStudentInfo.beActivistDate = moment(this.tempStudentInfo.beActivistDate).format("YYYY/MM/DD");
          let req = _.clone(this.tempStudentInfo);
          if(req.applyDate == ""){
            delete req.applyDate;
          }
          if(req.beDevelopDate == ""){
            delete req.beDevelopDate;
          }
          if(req.beActivistDate == ""){
            delete req.beActivistDate;
          }
          console.log(this.tempStudentInfo)
          $.post(`http://${ip}/stu/addOne`,req,function (response) {
            // util.redict(response);
            if(response.code == 1001){
              self.$message("");
              self.tempStudentInfo = {};
            }else{
              self.$message.error("");
            }
          })
        }
      },
      beforeMount:function () {

        /**
         * 
         * @type {beforeMount}
         */
        let self  = this;
        $.post(`http://${ip}/getSchools`,function (response) {
          // util.redict(response);
          if(response.code == 1001){
            self.parties = response.data;
          }else{
            console.log("")
          }
        })
      }
    }
</script>

<style scoped>
  h2{
    font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","",Arial,sans-serif;
    font-size:26px;
    font-weight: lighter;
    margin-bottom: 30px;
  }
  .upload{
    margin-top: 20px;
  }
  .downloadExcel a{
    text-decoration: none;
    color:black;
  }
  .downloadExcel{
    margin-bottom: 20px;
  }
</style>



