<template>
  <div>
    <h2></h2>
    <el-tabs v-model="activeName">
      <el-tab-pane label="" name="view">
        <el-table
          :data="administrator"
          stripe
          style="width: 100%">
          <el-table-column
            prop="no"
            label=""
            width="230">
          </el-table-column>
          <el-table-column
            prop="name"
            label=""
            width="220">
          </el-table-column>
          <el-table-column
            prop="school"
            label=""
            width="220">
          </el-table-column>
          <el-table-column
            prop="power"
            label=""
            width="220">
          </el-table-column>
          <el-table-column
            fixed="right"
            label=""
            width="320">
            <template slot-scope="scope">
              <el-row>
                <el-col><el-button type="text" size="small" @click="deleteSpecialAdmin(scope.row)"></el-button></el-col>
              </el-row>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
      <el-tab-pane label="" name="add">
        <el-row>
          <el-col :span="10" :offset="4">
            <el-form ref="tempAdminInfo" :model="tempAdminInfo" label-width="140px">
              <el-form-item label="">
                <el-input v-model="tempAdminInfo.no"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="tempAdminInfo.name"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-select v-model="tempAdminInfo.schoolNo" placeholder="">
                  <el-option
                    v-for="item in parties"
                    :key="item.no"
                    :label="item.name"
                    :value="item.no">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-col :span="12" :offset="10">
                <el-button @click="insertAdmin()"></el-button>
              </el-col>
            </el-form>
          </el-col>
        </el-row>

      </el-tab-pane>
    </el-tabs>

  </div>
</template>

<script>
  let config = require("../../config/config");
  const ip = config.ip;
  let moment = require("moment");
  let $ = require("jquery");
  let _ = require("underscore");
    export default {
        name: "admin",
      data(){
          return {
            tempAdminInfo:{

            },
            parties:[],
            administrator:[],
            options:[{
              value:0,
              key:""
            },
              {
                value:1,
                key:""
              }],
            activeName:"view"
          }
      },
      methods:{
          update(){
            let self = this;
            $.post(`http://${ip}/admin/list`,function(response){
              if(response.code == 1001){
                console.log(response.data);
                self.administrator = response.data;
                for(let i = 0;i < response.data.length;i++){
                  self.options.forEach(function (e) {
                    if(e.value == response.data[i].power){
                      self.administrator[i].power = e.key;
                    }
                  })
                }
              }else{
                self.$message.error("");
              }
            })
          },
        deleteSpecialAdmin(row){
          let self = this;
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(()=>{
            $.post(`http://${ip}/admin/delete`,{
              no:row.no
            },function(response){
              if(response.code == 1001){
                self.$message("")
                $.post(`http://${ip}/admin/list`,function(response){
                  if(response.code == 1001){
                    console.log(response.data);
                    self.administrator = response.data;
                    for(let i = 0;i < response.data.length;i++){
                      self.options.forEach(function (e) {
                        if(e.value == response.data[i].power){
                          self.administrator[i].power = e.key;
                        }
                      })
                    }
                  }else{
                    self.$message.error("");
                  }
                })
              }else{
                self.$message.error(`${response.msg}`)
              }
            })
          })
        },

        insertAdmin(){
          let self = this;
          $.post(`http://${ip}/admin/add`,{
            no:self.tempAdminInfo.no,
            name:self.tempAdminInfo.name,
            schoolNo:self.tempAdminInfo.schoolNo
          },function (response) {
            if(response.code == 1001){
              self.$message("");
              self.update();
            }else{
              self.$message.error(`${response.msg}`);
            }
          })
        }
      },
      beforeMount:function(){
          if(this.$store.power !== 0){
            this.$message.error("");
            this.$router.push("/");
            return;
          }
          let self = this;
        $.post(`http://${ip}/getSchools`,function (response) {
          if(response.code == 1001){
            self.parties = response.data;
          }else{
            console.log("")
          }
        })
          $.post(`http://${ip}/admin/list`,function(response){
            if(response.code == 1001){
              console.log(response.data);
              self.administrator = response.data;
              for(let i = 0;i < response.data.length;i++){
                self.options.forEach(function (e) {
                  if(e.value == response.data[i].power){
                    self.administrator[i].power = e.key;
                  }
                })
              }
            }else{
              self.$message.error("");
            }
          })
      }
    }
</script>

<style scoped>
  h2{
    font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","",Arial,sans-serif;
    font-size:26px;
    font-weight: lighter;
    margin-bottom: 30px;
  }
</style>



